@import "mixin";

@import "header";

body {
	margin: 0 auto;
	min-width: 1120px; }

.bunner {
	border-bottom: 2px solid lighten($text, 30%); }

.st-container {
	.logo {
		div {
			color: $text; } }
	nav {
		a {
			color: $text; } } }

.works {
	width: $width;
	max-width: 95%;
	margin: auto;
	nav {
		margin-bottom: 50px;
		a {
			color: $text; } }
	.pict {
		width: 100%;
		height: 378px;
		margin-bottom: 20px;
		background-size: cover; }
	.pict:nth-child(3) {
		background: url("../image/Portfolio-1.png") 50% 50% no-repeat; }
	.pict:nth-child(4) {
		background: url("../image/Portfolio-2.png") 50% 50% no-repeat; }
	.pict:nth-child(5) {
		background: url("../image/Portfolio-3.png") 50% 50% no-repeat; }
	input[type="submit"] {
		display: none; } }
@include site-h;

@import "footer";

