@import "mixin";

@media all and (max-width: 720px) {
	body {
		min-width: $w375; }

	.bunner {
		background: $white;
		max-width: $w375;
		margin: auto;
		position: relative;
		.ninja {
			background: url("../image/Menu-1.png") center center no-repeat;
 }			// display: block

		nav {
			height: 1330px;
			a {
				color: $white; }
			.logo {
				div {
					color: $white; } } }

		.st-effect-1.st-menu-open .st-effect-1.st-menu {
			visibility: visible;
			transform: translate3d(340%, 0, 0);
			z-index: 300; } }

	.site-h {
		margin: 40px 0 20px; }
	.works {
		max-width: $w375;
		nav {
			margin: 15px auto; }
		.pict:nth-child(3) {
			background: url("../image/Brilliant-Move.png") 50% 50%; }
		.pict:nth-child(4) {
			background: url("../image/Bobber.png") 50% 50%; }
		.pict:nth-child(5) {
			display: none; }
		input[type="submit"] {
			cursor: pointer;
			display: block;
			margin: 30px auto 40px;
			background: none;
			border: 2px solid $green;
			color: $green;
			font-size: 18px;
			font-family: "FuturaPT";
			text-transform: uppercase;
			padding: 10px 50px; }
		input[type="submit"]:active {
			background:  $green;
			color: $white; } } }











