@import "mixin";
footer {
	background: $text;
	padding: 30px	0;
	.foot {
		text-align: center;
		margin: auto;
		width: $width;
		max-width: 95%;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		.logo {
			display: inline-block;
			div {
				color: lighten($text, 60%);
				top: -25%;
				transform: translateY(25%); } }
		nav {
			display: inline-block;
			width: 60%;
			text-align: left;
			padding-top: 15px;
			li {
				color: lighten($text, 50%);
				top: 0%;
				transform: translateY(0%);
				margin-left: 4.7%; } }
		.society {
			i {
				padding-top: 15px;
				vertical-align: middle;
				margin-left: 10px;
				color: lighten($text, 50%); }
			i:hover {
				color: $green; } } }

	.phone {
		display: inline-block;
		margin-top: 30px;
		margin-right: 45px;
		font-family: "FuturaPT";
		font-size: 18px;
		color: darken($white, 10%);
		i {
			margin-right: 15px;
			color: $green; } }
	.contact {
		width: $width;
		max-width: 95%;
		margin: auto;
		color: darken($white, 10%);
		font-family: "FuturaPT";
		font-size: 14px;
		position: relative;
		top: -20px; } }

@media all and (max-width: 1025px) {

	footer {
		.foot {
			nav {
				width: 55%;
				margin-left: 3%;

				li {
					margin-left: 2.6%; } } } } }

@media all and (max-width: 720px) {

	footer {
		width: $w375;
		margin: auto;
		padding: 15px 0;
		.foot {
			.logo {
				display: none; }
			nav {
				width: 95%;
				display: block;
				text-align: center;
				margin: auto;
				li {
					margin: 0 10px 25px;
					font-size: 12px; } }
			.ph-e {
				width: 95%;
				display: block;
				margin: auto;
				text-align-last: justify;
				.phone {
					margin: 0;
					font-size: 14px; } }
			.society {
				margin: 0 auto 10px; } }
		.contact {
			font-size: 12px;
			text-align: center;
			margin: 10px auto;
			top: 0; } }

	.site-h {
		font-size: 22px;
		margin: 40px 0 30px; } }
